<template>
  <div
    :class="[
      isEditable ? 'cursor-pointer' : '',
      !selected
        ? 'rounded-full border-2 border-solid border-content-on-neutral-secondary bg-bg-neutral-raised'
        : 'border-2 border-solid border-constant-brand-primary',
    ]"
    class="flex h-[20px] w-[20px] items-center justify-center rounded-full"
  >
    <div
      v-if="selected"
      class="flex h-[12px] w-[12px] items-center justify-center rounded-full"
      :class="selected ? 'bg-constant-brand-primary' : ''"
    />
  </div>
</template>
<script>
export default {
  props: {
    selected: {
      required: false,
      type: Boolean,
      default: false,
    },
    isEditable: {
      required: false,
      type: Boolean,
      default: true,
    },
  },
};
</script>
